@import url(https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,400;0,700;1,400&family=Londrina+Solid:wght@900&display=swap);

html{font-size:18px}a{color:#8B677C}a:hover{color:#5B3547;text-decoration:none}body{font-family:"Jost",sans-serif;color:#EFEADD;overflow-x:hidden;background:url(/themthings.github.io/static/media/bg3.4bdd9a25.jpg) center bottom/cover no-repeat fixed;background-color:#050306}.sr-only{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);border:0}.container{width:975px;max-width:100%;margin:0 auto;padding-left:1rem;padding-right:1rem}#main{padding:2rem 0}

.hero{background:url(/themthings.github.io/static/media/herobg.ca5fc1c6.jpg) center bottom/auto 100% repeat-x;position:relative;overflow:hidden}.hero .hero-img{padding-bottom:70.92652%;background:url(/themthings.github.io/static/media/hero.188eb3e9.jpg) center bottom/100% auto no-repeat}.hero .container{text-align:center}.hero .container h1{position:absolute;font-size:4rem;top:50%;left:50%;transform:translate(-50%, -50%);text-shadow:0 0 10px rgba(0,0,0,0.5);color:#F57E63}

.banner{height:100px;width:100%;position:relative;overflow:hidden;background-color:#5B3547}.banner span{position:absolute;top:0;left:0;background:#5B3547 url(data:image/png;base64,) repeat-x left top/auto 80px;width:100%;height:100%;-webkit-animation:bannermove 15s linear infinite;animation:bannermove 15s linear infinite}@-webkit-keyframes bannermove{0%{background-position:975px center}100%{background-position:-975px center}}@keyframes bannermove{0%{background-position:975px center}100%{background-position:-975px center}}

.section{padding:6rem 0;overflow:hidden}.section header h2{font-family:"Londrina Solid",sans-serif;line-height:1;font-size:6rem;font-weight:bold;text-transform:uppercase;margin:0;color:transparent;-webkit-text-stroke-color:#EFEADD;-webkit-text-stroke-width:2px;text-align:center}@media (min-width: 900px){.section header h2{text-align:left}}.section main{padding:0 2rem}


.video{position:relative;display:block;padding:0;margin-bottom:2rem}@media (min-width: 900px){.video+.video{margin-top:-6rem}}.video video{width:600px;height:auto;max-width:100%;will-change:transform}.video video::-internal-media-controls-overlay-cast-button{display:none}.video>div{position:absolute;top:50%;padding:1rem;left:-2rem;transform:translateY(-10%)}.video>div>*{position:relative;z-index:2}.video>div:after{content:'';position:absolute;top:0;left:-25%;height:100%;width:150%;background-color:black;transform:skewX(10deg);z-index:1}.video>div::before{content:'';position:absolute;top:0;left:auto;right:-15%;height:100%;width:150%;background-color:#726567;transform:skewX(10deg);z-index:0}.video:nth-child(even){text-align:right}.video:nth-child(even)>div{right:-2rem;left:auto}.video:nth-child(even)>div::after{transform:skewX(-10deg)}.video:nth-child(even)>div::before{right:auto;left:-15%;transform:skewX(-10deg)}

.bit-logo-container{display:none}.bit-widget .bit-no-dates-container .bit-track-button{max-width:100%}

#footer{background-color:#050306;position:relative;color:white;padding:2rem 0;text-align:center;line-height:3}#footer:before{content:'';position:absolute;top:-100px;left:0;height:100px;width:100%;background:url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAABkCAYAAABHLFpgAAAAN0lEQVR4AbWRgQYAIBQD1/b0/5+cAkEHenDODTBVpixpbPhlxnYQaGDB9gfFA68dMDQwY+PLLlvPOgHSzuktdQAAAABJRU5ErkJggg==) repeat-x}#footer .logo{width:150px;margin-bottom:1rem}#footer .copyright{font-size:.75rem}

.social-media-links{width:200px;max-width:100%;margin:0 auto;display:flex;justify-content:space-between;font-size:1.5em;line-height:1}

.icon svg{-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased;display:inline-block;font-style:normal;font-feature-settings:normal;font-variant:normal;text-rendering:auto;line-height:1;width:0.875em;height:1em;font-size:inherit;vertical-align:-0.125em;overflow:visible}

